#MainHeader {
  background: #fcfcd4;
  position: fixed;
  height: 100%;
  width: 100%;
  max-width: 500px;
  max-height: 30px;
}
#MainHeader #MainTitle {
  display: inline-block;
  padding-bottom: 0%;
}
#MainHeader #MainTitle #MainHeaderTitle {
  color: #00a8d6;
  font-size: 100%;
  background-color: #fcfcd4;
}
#MainHeader #MainTitle #MainHeaderTitlehover {
  color: purple !important;
}
#MainHeader #SocialDiv {
  float: right;
}
#MainHeader #SocialList {
  position: absolute;
  list-style-type: none;
  margin: 0;
  padding: 0;
  right: 0px;
  top: 0px;
}
#MainHeader #SocialList a {
  display: inline-block;
}
#MainHeader .Socials {
  display: flex;
  flex-direction: row;
  background: #fcfcd4;
}
#MainHeader .Socials .pictureWindow {
  height: 30px;
  width: 30px;
  z-index: 4;
}
#MainHeader .Socials .pictureLogo {
  height: 30px;
  width: 30px;
  position: absolute;
  margin-top: 1%;
  margin-right: 1%;
  z-index: 3;
}
#MainHeader .Socials .midDrop {
  height: 20px;
  width: 20px;
  margin-top: 3%;
  margin-left: 5%;
  border-radius: 50%;
  position: absolute;
  z-index: 2;
}
#MainHeader .Socials .backDrop {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  margin-top: 3%;
  margin-left: 5%;
  position: absolute;
  z-index: 1;
}

#NavBodyDiv {
  position: fixed;
  height: 100%;
  width: 100%;
  padding-top: 7%;
  display: inline-block;
  max-width: 500px;
  max-height: 200px;
  margin: 0%;
}
#NavBodyDiv #MainLeftNav {
  height: 100%;
  width: 10%;
  float: left;
  position: relative;
}
#NavBodyDiv #NavBarContainer {
  width: 20%;
}
#NavBodyDiv #NavBarContainer .NavButtonsList {
  padding-top: 10%;
}
#NavBodyDiv #NavBarContainer .NavButtonsList .NavButtons {
  background-color: #e2feff;
  color: black;
  border-color: #00a8d6;
  border-radius: 20%;
  height: 10%;
  width: 100%;
}

body {
  background-color: #00a8d6;
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100%;
  width: 80%;
  max-width: 500px;
  max-height: 740px;
  margin-trim: 100%;
}
body h1 {
  font-size: 15px;
  color: #fcfcd4;
}
body p {
  color: #fcfcd4;
}

.MainBody {
  padding-left: 17%;
  padding-bottom: 0%;
  top: 15%;
  position: absolute;
  float: right;
  z-index: -1;
  width: 100%;
  height: 100%;
  list-style-type: none;
}

#MainBody-Home {
  width: 80%;
  height: 3000px;
}
#MainBody-Home h1 {
  padding-left: 10%;
}

#MainBody-AboutMe {
  height: 100%;
  width: 90%;
}
#MainBody-AboutMe h1 {
  padding-left: 5%;
}
#MainBody-AboutMe .ListTestingGallery {
  list-style-type: none;
  display: block;
}
#MainBody-AboutMe .ListTestingDivider {
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.1px);
  border-radius: 15px;
  -webkit-backdrop-filter: blur(5.1px);
  height: 100%;
  width: 100%;
}
#MainBody-AboutMe .aboutMePic {
  list-style-type: none;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
#MainBody-AboutMe .aboutMeText {
  list-style-type: none;
  color: #fcfcd4;
  font-size: 50%;
  height: 100%;
  width: 50%;
  line-height: 0.1px;
}

.GameBodyMainContainer {
  width: 90%;
  height: 100%;
  display: block;
}
.GameBodyMainContainer h1 {
  padding-left: 5%;
}
.GameBodyMainContainer #GameBodyClass-containerElement {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameBodyUL {
  width: 100%;
  height: 100%;
  list-style-type: none;
  padding-bottom: 5%;
  display: inline-flex;
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameBodyUL .GameBodyLI {
  height: 100%;
  width: 100%;
  padding-top: 1%;
  padding-bottom: 1%;
  padding-left: 1%;
  padding-right: 1%;
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer {
  list-style-type: none;
  position: relative;
  height: 100%;
  width: 80%;
  border: 0%;
  padding-top: 10%;
  padding-bottom: 10%;
  padding-left: 2%;
  padding-right: 2%;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.1px);
  border-radius: 15px;
  -webkit-backdrop-filter: blur(5.1px);
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockImage {
  height: 70%;
  width: 100%;
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockTitle {
  font-size: 5%;
  padding-left: 5%;
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockDescription {
  font-size: 5%;
  padding-left: 5%;
}
.GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockButton {
  background-color: #e2feff;
  font-size: 5%;
  text-align: center;
  color: black;
  border-color: #00a8d6;
  border-radius: 20%;
  height: 100%;
  width: 100%;
  margin: auto;
  text-align: center;
  float: top;
}

#MainBody-Photos {
  display: inline-block;
  height: 740px;
  width: 500px;
  margin: 0%;
  height: 100%;
  width: 100%;
}
#MainBody-Photos h1 {
  font-size: 60%;
  padding-left: 5%;
}
#MainBody-Photos #PhotoBodyClass-containerElement {
  position: relative;
  height: 100%;
  width: 80%;
  overflow: scroll;
  padding-left: 0%;
  margin: 0px;
}
#MainBody-Photos #PhotoBodyCarouselContainerScrollElement {
  height: 20%;
  width: 90%;
  padding-left: 5%;
}
#MainBody-Photos #PhotoBodyCarouselContainer {
  margin-bottom: 100%;
  width: 100%;
  height: 100%;
}
#MainBody-Photos .PhotoBodyCarouselPicture {
  height: 60%;
  width: 80%;
}
#MainBody-Photos .ScrollElementDiv {
  padding-left: 10%;
  width: 100%;
}
#MainBody-Photos .PhotoUL {
  list-style-type: none;
  height: 80%;
  width: 100%;
  margin-right: 0%;
  padding-top: 2%;
}
#MainBody-Photos .PhotoUL .PhotoLI {
  width: 120%;
  position: relative;
}
#MainBody-Photos .DisplayBlockMainContainer {
  height: 100%;
  width: 60%;
  position: relative;
}
#MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer {
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.1px);
  border-radius: 20%;
  -webkit-backdrop-filter: blur(5.1px);
  height: 80%;
  width: 45%;
  display: inline-block;
  margin-inline: 2%;
}
#MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer .DisplayBlockTitle {
  color: #fcfcd4;
  font-size: 100%;
  text-align: center;
}
#MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer .DisplayBlockImage {
  padding-right: 10%;
  padding-left: 10%;
  height: 60%;
  width: 80%;
}
#MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer .DisplayBlockButton {
  background-color: #e2feff;
  font-size: 55%;
  color: black;
  border-color: #00a8d6;
  border-radius: 20%;
  height: 20%;
  width: 50%;
  display: grid;
  margin: auto;
}

#MainBody-3DModels {
  height: 740px;
  width: 80%;
}
#MainBody-3DModels #ModelBodyClass-containerElement {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: scroll;
  display: inline-block;
}
#MainBody-3DModels #ModelBodyClass-containerElement h1 {
  padding-left: 8%;
}
#MainBody-3DModels #ModelBodyClass-containerElement .ModelDisplayBlockMainContainter {
  display: inline-flex;
  width: 100%;
  height: 100%;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL {
  padding-left: 5%;
  display: flex;
  list-style-type: none;
  align-items: center;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockImageLI {
  padding-right: 0%;
  padding-left: 0%;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockImageLI .ModelDisplayBlockImage {
  height: 50%;
  width: 80%;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayButtonLI {
  height: 100%;
  width: 100%;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayButtonLI .ModelDisplayBlockButton {
  background-color: #e2feff;
  font-size: 5%;
  text-align: center;
  color: black;
  border-color: #00a8d6;
  border-radius: 20%;
  height: 100%;
  width: 100%;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockTextContainer {
  padding-left: 10%;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockTextContainer .ModelDisplayBlockTitle {
  color: #fcfcd4;
}
#MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockTextContainer .ModelDisplayBlockDescription {
  color: #fcfcd4;
}

#Footer {
  background: #fcfcd4;
  bottom: 0px;
  position: fixed;
  margin: 0px;
  height: 5%;
  width: 100%;
  max-width: 500px;
  max-height: 50px;
}
#Footer h1 {
  background: none;
  color: #00a8d6;
  font-size: 10px;
  width: 20%;
  margin: 0px;
}
#Footer #FooterUL {
  display: inline-block;
}
#Footer #FooterUL .FooterItems ul li {
  display: inline-block;
  position: relative;
}
#Footer #FooterUL .FooterItems ul li h2 {
  padding-left: 5%;
  font-size: 60%;
}
#Footer #FooterUL .FooterItems ul li h3 {
  padding-left: 5%;
  font-size: 40%;
}
#Footer #SocialDiv {
  float: right;
}
#Footer #SocialList {
  position: absolute;
  list-style-type: none;
  margin: 0;
  padding: 0;
  right: 0px;
  bottom: 0px;
  background: none;
}
#Footer #SocialList a {
  display: inline-block;
}
#Footer #SocialList .FooterItems a {
  font-size: xx-large;
}
#Footer #SocialList .Socials {
  display: flex;
  flex-direction: row;
  background: #fcfcd4;
}
#Footer #SocialList .Socials .pictureWindow {
  height: 30px;
  width: 30px;
  z-index: 4;
  background: none;
}
#Footer #SocialList .Socials .pictureLogo {
  height: 30px;
  width: 30px;
  position: absolute;
  background: none;
  margin-top: 1%;
  margin-left: 1%;
  z-index: 3;
}
#Footer #SocialList .Socials .midDrop {
  height: 20px;
  width: 20px;
  margin-top: 3%;
  margin-left: 5%;
  border-radius: 50%;
  position: absolute;
  z-index: 2;
}
#Footer #SocialList .Socials .backDrop {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  margin-top: 5%;
  margin-left: 5%;
  position: absolute;
  z-index: 1;
}

* {
  padding: 0;
  margin: 0%;
}

@media only screen and (min-width: 1000px) {
  #MainHeader {
    max-width: 2000px;
    max-height: 80px;
  }
  #MainHeader #MainTitle #MainHeaderTitle {
    color: red;
    font-size: 400%;
  }
  #MainHeader .Socials .pictureWindow {
    height: 80px;
    width: 80px;
  }
  #MainHeader .Socials .pictureLogo {
    height: 80px;
    width: 80px;
  }
  #MainHeader .Socials .midDrop {
    height: 50px;
    width: 50px;
  }
  #MainHeader .Socials .backDrop {
    height: 50px;
    width: 50px;
  }
  #NavBodyDiv {
    max-width: 2000px;
    max-height: 500px;
    padding-top: 10%;
  }
  #NavBodyDiv #NavBarContainer {
    max-width: 2000px;
    height: 100%;
    width: 10%;
  }
  #NavBodyDiv #NavBarContainer .NavButtonsList {
    padding-top: 6%;
  }
  #NavBodyDiv #NavBarContainer .NavButtonsList .NavButtons {
    height: 50px;
    width: 200px;
  }
  body {
    height: 100%;
    width: 80%;
    max-width: 2000px;
    max-height: 1000px;
  }
  body h1 {
    font-size: 200%;
  }
  .MainBody {
    width: 80%;
    height: 100%;
  }
  #MainBody-Home h1 {
    color: red;
  }
  #MainBody-AboutMe {
    height: 100%;
    width: 80%;
  }
  #MainBody-AboutMe h1 {
    font: 200%;
  }
  #MainBody-AboutMe .ListTestingDivider {
    height: 100%;
    width: 100%;
  }
  #MainBody-AboutMe .aboutMePic {
    height: 30%;
    width: 60%;
  }
  #MainBody-AboutMe .aboutMeText {
    font-size: 100%;
    height: 100%;
    width: 50%;
  }
  .GameBodyMainContainer {
    height: 100%;
    width: 105%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement {
    height: 100%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer {
    height: 80%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockImage {
    height: 100%;
    width: 100%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockTitle {
    font-size: 120%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockDescription {
    font-size: 80%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockLI {
    height: 100%;
  }
  .GameBodyMainContainer #GameBodyClass-containerElement .GameDisplayBlockMainContainer .GameDisplayBlockLI .GameDisplayBlockButton {
    height: 30px;
    width: 100%;
  }
  #MainBody-Photos h1 {
    font-size: 200%;
  }
  #MainBody-Photos #PhotoBodyClass-containerElement {
    height: 183%;
    width: 80%;
    padding-bottom: 5%;
  }
  #MainBody-Photos #PhotoBodyCarouselContainerScrollElement {
    width: 95%;
    height: 33%;
    padding-bottom: 5%;
  }
  #MainBody-Photos .PhotoBodyCarouselPicture {
    width: 100%;
  }
  #MainBody-Photos .DisplayBlockMainContainer {
    margin-right: 0px;
  }
  #MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer {
    margin-inline: 2.5%;
  }
  #MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer .DisplayBlockTitle {
    font-size: 200%;
  }
  #MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer .DisplayBlockImage {
    width: 80%;
    height: 100%;
  }
  #MainBody-Photos .DisplayBlockMainContainer .DisplayBlockChildContainer .DisplayBlockButton {
    width: 50%;
  }
  #MainBody-3DModels {
    width: 83%;
    height: 100%;
  }
  #MainBody-3DModels #ModelBodyClass-containerElement {
    width: 100%;
    height: 168%;
  }
  #MainBody-3DModels #ModelBodyClass-containerElement .ModelDisplayBlockMainContainter {
    width: 100%;
    height: 100%;
  }
  #MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockImageLI {
    height: 80%;
    width: 30%;
  }
  #MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayBlockImageLI .ModelDisplayBlockImage {
    height: 80%;
    width: 100%;
  }
  #MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayButtonLI {
    width: 400%;
    height: 400%;
  }
  #MainBody-3DModels #ModelBodyClass-containerElement #ModelDisplayBlockUL .ModelDisplayButtonLI .ModelDisplayBlockButton {
    width: 400%;
    height: 400%;
  }
  #Footer {
    max-width: 2000px;
    max-height: 1000px;
    width: 100%;
    height: 6.8%;
  }
  #Footer h1 {
    font-size: 200%;
  }
  #Footer #FooterUL .FooterItems ul li h2 {
    font-size: 100%;
  }
  #Footer #FooterUL .FooterItems ul li h3 {
    font-size: 100%;
  }
  #Footer #SocialList .Socials .pictureWindow {
    height: 80px;
    width: 80px;
  }
  #Footer #SocialList .Socials .pictureLogo {
    height: 80px;
    width: 80px;
  }
  #Footer #SocialList .Socials .midDrop {
    height: 50px;
    width: 50px;
  }
  #Footer #SocialList .Socials .backDrop {
    height: 50px;
    width: 50px;
  }
}

